export class CryptoHelper {
    public static generateRandom(nums: Array<number>) {
        const characters = '0123456789abcdefghijklmnopqrstuvwxyz';
        const sum = nums.reduce((prev, cur) => prev + cur, 0);
        const isomorphicCrypto: typeof crypto = typeof crypto != "undefined" ? eval("crypto") : eval("require")("crypto");
        const randomValues = isomorphicCrypto.getRandomValues(new Uint8Array(sum));
        let index = 0;
        return nums.reduce((prev, num) => {
            prev.push(Array.from(new Array(num)).map(_ => characters[randomValues[index++] % characters.length]).join(""));
            return prev;
        }, [] as Array<string>);
    }
    /**
     * be945482-jf10-40d1-a50d-rzxa2c41fdcu
     * (   8    - 4  - 4  - 4  - 3 ) + 9(time)(1970 - 5186)
     */
    public static CreateGuid() {
        const timePart = Date.now().toString(36).substring(0, 9).padStart(9, "0");
        const randomPart = CryptoHelper.generateRandom([8, 4, 4, 4, 3]).join("-");
        return randomPart + timePart;
    }
}